import React, { useEffect, useState } from "react";
import "./Flash_Deal.css";

const Flash_Deal = () => {
  const [countdown, setCountdown] = useState(" ");

  useEffect(() => {
    const countDownDate = new Date("December 31, 2025").getTime();

    const interval = setInterval(() => {
      const now = new Date().getTime();
      const distance = countDownDate - now;

      if (distance < 0) {
        clearInterval(interval);
        setCountdown("EXPIRED");
        return;
      }

      const days = Math.floor(distance / (1000 * 60 * 60 * 24));
      const hours = String(
        Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60))
      ).padStart(2, "0");
      const minutes = String(
        Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60))
      ).padStart(2, "0");
      const seconds = String(
        Math.floor((distance % (1000 * 60)) / 1000)
      ).padStart(2, "0");

      setCountdown(`${days} Days ${hours} : ${minutes} : ${seconds}`);
    }, 1000);

    return () => clearInterval(interval); // Cleanup on component unmount
  }, []);

  return (
    <div className="flash_deal">
      <div className="flash_deal_inner">
        <div className="flash_deal_part sec1">
          <img src="/Porto/images/shop21_featured_sale.jpg" alt="Flash Deal" />
          <h2>Flash Deals</h2>
          <p>SALE</p>
          <p>-49%</p>
        </div>
        <div className="flash_deal_part sec2">
          <a href="/">1080p Wifi IP Camera</a>
          <span className="flash_deal_part_rating">
            <i className="fa-regular fa-star"></i>
            <i className="fa-regular fa-star"></i>
            <i className="fa-regular fa-star"></i>
            <i className="fa-regular fa-star"></i>
            <i className="fa-regular fa-star"></i>
          </span>
          <div className="flash_deal_part sec3">
            <p>$596.00</p>
            <p>$299.00</p>
          </div>
          <div className="flash_deal_part sec4">
            <span>OFFERS ENDS IN:</span>
            <p>{countdown}</p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Flash_Deal;
