import React, { useState } from "react";
import "./Cart.css"
import "../../Component/loader/loader.css"

const Cart3 = ({ sectionTitle, categories, products }) => {
  const [imageLoaded, setImageLoaded] = useState(false);

  const handleImageLoad = () => {
    setTimeout(() => {
      setImageLoaded(true);
    }, 2000); // Minimum 2-second delay before showing the image
  };

  return (
    <div className="cart3 section">
      <div className="row">
        {products.map((product, index) => (
          <div className="product" key={index}>
            <div className="product-img">
              {/* <img src={product.images} alt={product.name} /> */}
              {!imageLoaded && (
                <div className="image-loader">
                  <i className="fa-solid fa-compact-disc fa-spin"></i>
                </div>
              )}
              <img
                src={product.images}
                alt={product.name}
                onLoad={handleImageLoad} // Apply delay on load                
                onError={(e) => {
                  e.target.src = "/Porto/images/default.jpg"; // Fallback image
                  setImageLoaded(true);
                }}
                style={{ display: imageLoaded ? "block" : "none" }} // Hide image until loaded
              />

              <div className="product-label">
                {product.sale && (
                  <span className="sale">{product.sale}</span>
                )}
                {product.isNew && <span className="new">NEW</span>}
              </div>
            </div>
            <div className="product-body">
              <p className="product-category">{product.category}</p>
              <h3 className="product-name">
                <a href="#">{product.name}</a>
              </h3>
              <h4 className="product-price">
                {product.price}{" "}
                {product.oldPrice && (
                  <del className="product-old-price">
                    {product.oldPrice}
                  </del>
                )}
              </h4>
              <div className="product-rating">
                {Array.from({ length: 5 }).map((_, i) => (
                  <i
                    key={i}
                    className={`${i < product.rating ? "fa-solid fa-star" : "fa-regular fa-star"
                      }`}
                  ></i>
                ))}
              </div>
              <div className="product-btns">
                <button className="add-to-wishlist">
                  <i className="fa-regular fa-heart"></i>
                  <span className="tooltipp">add to wishlist</span>
                </button>
                <button className="add-to-compare">
                  <i className="fa fa-exchange"></i>
                  <span className="tooltipp">add to compare</span>
                </button>
                <button className="quick-view">
                  <i className="fa fa-eye"></i>
                  <span className="tooltipp">quick view</span>
                </button>
              </div>
            </div>
            <div className="add-to-cart">
              <button className="add-to-cart-btn">
                <i className="fa fa-shopping-cart"></i> add to cart
              </button>
            </div>
          </div>
        ))}
      </div>
      {/* <div id="slick-nav-1" className="products-slick-nav"></div> */}
    </div>
    //     </div>
    //   </div>
    // </div>
    //   </div>
  );
};

export default Cart3;
