import React, { useEffect, useState } from 'react'
import Cart3 from './Cart3';

const   Cart_section9 = () => {
    const [products, setProducts] = useState([]);
    const [loading, setLoading] = useState(true); 
    const [error, setError] = useState(null); 

    const sectionTitle = "sunglasses";

    useEffect(() => {
        // Fetch smartphones from API
        const fetchProducts = async () => {
            try {
                const response = await fetch("https://dummyjson.com/products/category/sunglasses");
                if (!response.ok) {
                    throw new Error("Failed to fetch smartphones");
                }
                const data = await response.json();

                // Map products to include only the necessary data and limit to 8 items
                const mappedProducts = data.products.slice(0,20).map((product, index) => ({
                    id: product.id,
                    name: product.title,
                    description: product.description,
                    price: product.price,
                    rating: product.rating,
                    images: product.thumbnail,
                    //   images: [
                    //     `/Porto/images/products_1_${index + 0}.jpg`, 
                    //   ],

                }));

                setProducts(mappedProducts); // Update state with mapped products
                setLoading(false);
            } catch (err) {
                setError(err.message);
                setLoading(false);
            }
        };

        fetchProducts();
    }, []); // Empty dependency array ensures the effect runs only once

    if (loading) {
        return <div>Loading smartphones...</div>; // Loading indicator
    }

    if (error) {
        return <div>Error: {error}</div>; // Error message
    }

    return (
        <div className="cart_section_title cart_section9">
            <h3>{sectionTitle.replace("-", " ")}</h3>
            <Cart3 sectionTitle={sectionTitle} products={products} />
        </div>
    );
};


export default   Cart_section9;